@use "../base/vars";

.hero {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 4rem;
    padding-block: 20rem;

    &__title, &__subtitle {text-align: center}
    &__subtitle{
        line-height: 1.3;
        color: vars.$greyColor;
    }
    &--service{
        // display: flex;
        // flex-direction: row;
        // align-content: space-between;
        & > .hero {
            
            &__subtitle{
            font-family: vars.$mainFontMedium;
            color: #000;
            }
            &__details{
                max-width: 48%;
                display: flex;
                flex-direction: column;

                gap: 5rem;
                text-align: left;
                & > h2, & > p {
                    text-align: left;
                }
            }
        }
    }
}